<template>
  <div>
    <van-cell
      class="mt10"
      title="项目名称"
      :value="dataFrom.billType"
    />
    <van-cell
      title="交易时间"
      :value="dataFrom.createTime"
    />
    <van-cell
      title="操作人"
      :value="dataFrom.person"
    />
    <van-cell
      class="mt10"
      title="交易金额"
      :value-class="dataFrom.amount > 0 ? 'bc6c' : 'ff5'"
      :value="dataFrom.amount > 0 ? '+¥' + dataFrom.amount.toFixed(2) : '-¥' + dataFrom.amount.toFixed(2) "
    />
    <van-cell
      title="账户结余"
      :value="'¥' + dataFrom.leftAmount.toFixed(2)"
    />
  </div>
</template>
<script>
import { GetBusinessHotelPayeeAccount } from '@/api/api_business';
export default {
    data() {
        return {
            dataFrom: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '账单明细');
        this.dataFrom = this.$route.query;
    },
    methods: {}
};
</script>
<style scoped>
.mt10 {
    margin-top: 10px;
}
/deep/ .van-cell__title {
    text-align: left;
    color: #000;
}
/deep/ .van-cell__value {
    color: #000;
}
.bc6c {
    color: #02bc6c;
}
.ff5 {
    color: #ff5257;
}
</style>



